.sk-cta-phone.sk-block {
  padding-top: 50px;
  padding-bottom: 50px;
}
@media screen and (max-width: 1024px) {
  .sk-cta-phone .text-highlight {
    font-size: 24px;
    max-width: 225px;
  }
  .sk-cta-phone .open-form-modal {
    width: 100%;
  }
  .sk-cta-phone .phone-icon {
    margin: auto;
  }
}
.sk-cta-phone a[href^="tel:"]:not(.btn-default) {
  color: #17fcc4 !important;
  white-space: nowrap;
}
.sk-cta-phone .phone-icon {
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%;
}
.sk-cta-phone .phone-icon::before {
  width: 48px;
  height: 48px;
}